import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import DetalhesDep from '@/components/DetalhesDep'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/deputado/:id',
      name: 'DetalhesDep',
      component: DetalhesDep
    }
  ]
})



